<template>
  <v-container fill-height>
    <v-card>
      <v-card-title class="title">{{ project.title}}</v-card-title>
      <v-card-text class="pl-4">
        <span>{{ project.subtitle }}</span>
        <div v-html="project.body_html"></div>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn flat class="light-blue--text" @click="$router.back()">Back</v-btn>
      </v-card-actions>
    </v-card>
  </v-container>
</template>
<script>
  import postservice from '@/services/postservice'

  export default {
    props: {
      project_id_prop: {
        type: String,
        default: null
      }
    },
    data () {
      return {
        project: {
          id: null,
          title: null,
          subtitle: null,
          body: null
        }
      }
    },
    created () {
      postservice.getProject(this.project_id_prop).then(response => {
        this.project = response.data.project
      })
    }
  }
</script>



